}

.os-no-css-vars.os-theme-light:active .os-scrollbar-handle {
  background: hsla(0, 0%, 100%, 0.66);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  padding-top: 10.125rem;
  min-height: 100vh;
  font-family: sans-serif;
  font-weight: 400;
  color: var(--black-color);
  background-color: var(--white-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea {
  padding: 0;
  font: inherit;
  color: inherit;
  background-color: transparent;
}

button {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

:root {
  --white-color: #fff;
  --black-color: #000;
  --primary-color: #17696a;
  --success-color: #03cea4;
  --danger-color: #ff4242;
  --warning-color: #f89828;
  --info-color: #5a87fc;
  --background-color: #e5e5e5;
  --gray900-color: #1e212c;
  --gray800-color: #424551;
  --gray700-color: #787a80;
  --gray600-color: #9a9ca5;
  --gray500-color: #b3b7bc;
  --gray400-color: #d7dadd;
  --gray300-color: #e5e8ed;
  --gray200-color: #f4f5f6;
  --gray100-color: #f9f9f9;
  --graywhite-color: rgb(255 255 255 / 60%);
  --input-hover-color: rgb(23 105 106 / 40%);
  --button-hover-color: #145c5d;
  --display1: 900 4.5rem / 130% "Lato", sans-serif;
  --display2: 900 4rem / 130% "Lato", sans-serif;
  --display3: 900 3.5rem / 130% "Lato", sans-serif;
  --display4: 900 3rem / 130% "Lato", sans-serif;
  --lead-regular: 400 1.25rem / 150% "Lato", sans-serif;
  --lead-bold: 700 1.25rem / 150% "Lato", sans-serif;
  --large-regular: 400 1.125rem / 150% "Lato", sans-serif;
  --large-bold: 700 1.125rem / 150% "Lato", sans-serif;
  --base-regular: 400 1rem / 160% "Lato", sans-serif;
  --base-bold: 700 1rem / 160% "Lato", sans-serif;
  --small-regular: 400 0.875rem / 150% "Lato", sans-serif;
  --small-bold: 700 0.875rem / 150% "Lato", sans-serif;
  --extrasmall-regular: 400 0.75rem / 150% "Lato", sans-serif;
  --extrasmall-bold: 700 0.75rem / 150% "Lato", sans-serif;
  --extra2small-regular: 400 0.625rem / 150% "Lato", sans-serif;
  --extra2small-bold: 900 0.625rem / 150% "Lato", sans-serif;
  --divider-gradient: linear-gradient(rgb(218 219 221 / 0%) 0%, rgb(218 219 221 / 50%) 12.33%, #dadbdd 51.91%, rgb(218 219 221 / 50%) 87.85%, rgb(218 219 221 / 0%) 100%);
  --small-shadow: 0 4px 4px rgb(30 33 44 / 3%), 0 12px 10px rgb(154 156 165 / 4%), 0 30px 24px rgb(154 156 165 / 5%), 0 80px 80px rgb(154 156 165 / 8%);
  --regular-shadow: 0 4px 4px rgb(30 33 44 / 5%), 0 12px 10px rgb(154 156 165 / 8%), 0 30px 24px rgb(154 156 165 / 10%), 0 80px 80px rgb(154 156 165 / 16%);
  --large-shadow: 0 4px 4px rgb(30 33 44 / 5%), 0 12px 10px rgb(30 33 44 / 8%), 0 26px 24px rgb(30 33 44 / 10%), 0 60px 80px rgb(30 33 44 / 16%);
  --main-shadow: 0 4px 4px rgb(0 0 0 / 25%);
  --z-modal: 1000;
  --z-tooltip: 100;
  --z-fixed: 10;
}

.container {
  margin-inline: 1.875rem;
  max-width: 76.875rem;
}
@media (min-width: 80.625em) {
  .container {
    margin-inline: auto;
  }
}

.input {
  outline: none;
  border: 1px solid var(--gray400-color);
  border-radius: 0.25rem;
  padding: 0.6875rem 1rem;
  font: var(--small-regular);
  color: var(--gray800-color);
}
.input::placeholder {
  font: var(--small-regular);
  color: var(--gray600-color);
}
.input:focus {
  border-color: var(--input-hover-color);
  box-shadow: var(--main-shadow);
}

.btn {
  display: inline-flex;
  column-gap: 0.5rem;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--primary-color);
  border-radius: 0.25rem;
  padding-inline: 1.5rem;
  height: 2.25rem;
  font: var(--extrasmall-bold);
  letter-spacing: 0.03125rem;
  color: var(--white-color);
  background-color: var(--primary-color);
  transition: all 0.3s ease;
}
.btn svg {
  fill: currentcolor;
}
.btn:hover {
  background-color: var(--button-hover-color);
}
.btn:active {
  background-color: var(--button-hover-color);
}
.btn:disabled {
  opacity: 0.5;
}
.btn--mid {
  column-gap: 0.5rem;
  padding-inline: 2rem;
  height: 2.75rem;
  font: var(--small-bold);
}
.btn--lg {
  padding-inline: 2.5rem;
  height: 3.25rem;
  font: var(--base-bold);
}
.btn--outline {
  color: var(--primary-color);
  background-color: var(--white-color);
}
.btn--outline:hover {
  color: var(--white-color);
  background-color: var(--primary-color);
}
.btn--outline:active {
  color: var(--white-color);
  background-color: var(--primary-color);
}
.btn--outline:disabled {
  opacity: 0.5;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.header-top {
  padding-top: 0.5625rem;
  padding-bottom: 0.625rem;
  font: var(--small-regular);
  color: var(--graywhite-color);
  background-color: var(--gray900-color);
}
.header-top__container {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1.54fr 4.9fr 1fr 1fr;
}
.header-top__available {
  justify-self: self-start;
  white-space: nowrap;
}
.header-top__nav {
  white-space: nowrap;
}
.header-top__currency {
  white-space: nowrap;
}
.header-top__login {
  justify-self: self-end;
  white-space: nowrap;
}

.available {
  transition: color 0.3s ease;
}
.available__text {
  margin-right: 0.1875rem;
}
.available__tel {
  font: var(--small-bold);
}
.available:hover {
  color: var(--white-color);
}

.top-nav__list {
  display: flex;
  column-gap: 2rem;
}
.top-nav__link {
  transition: color 0.3s ease;
}
.top-nav__link:hover {
  color: var(--white-color);
}

.currency {
  display: flex;
  column-gap: 0.40625rem;
  align-items: center;
  width: 6.5625rem;
  word-spacing: 0.0875rem;
}
.currency__flag {
  width: 1.25rem;
  height: 0.75rem;
}

.custom-select {
  position: relative;
  overflow: hidden;
  user-select: none;
}
.custom-select__head {
  display: flex;
  align-items: center;
  padding: 0.125rem 0.125rem;
  transition: color 0.3s ease;
  cursor: pointer;
}
.custom-select__head:hover {
  color: var(--white-color);
}
.custom-select__selected {
  padding-inline: 0.1875rem;
  line-height: 1.3125rem;
  cursor: pointer;
}
.custom-select__inner-wrapper {
  position: absolute;
  right: 0;
  top: 110%;
  z-index: var(--z-tooltip);
  border-radius: 0.25rem;
  padding-right: 0.3125rem;
  padding-left: 0.9375rem;
  padding-block: 0.625rem;
  width: 7.5rem;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  background-color: var(--white-color);
}
.custom-select__list {
  overflow-y: auto;
  border-radius: 0.25rem;
  width: 100%;
  max-height: 12.5rem;
  color: var(--gray900-color);
  background-color: var(--white-color);
}
.custom-select__item {
  padding: 0.25rem 0.3125rem;
  width: 100%;
  transition: color 0.3s ease;
  cursor: pointer;
}
.custom-select__item:hover {
  color: var(--primary-color);
}
.custom-select__item--active {
  color: var(--primary-color);
}
.custom-select svg {
  position: relative;
  top: 0.03125rem;
  width: 0.5625rem;
  height: 0.3125rem;
  fill: currentcolor;
  transition: transform 0.1s, rotate 0.1s;
}
.custom-select--open {
  overflow: visible;
}
.custom-select--open svg {
  top: 0;
  transform: rotate(180deg);
}

.login {
  position: relative;
  display: flex;
  align-items: center;
  color: inherit;
}
.login::before {
  content: "/";
  position: absolute;
  left: 61px;
  top: 47%;
  transform: translateY(-50%);
}
.login__icon {
  order: -1;
  margin-right: 0.59375rem;
  width: 0.8125rem;
  height: 0.875rem;
  fill: currentcolor;
  transition: fill 0.3s ease;
}
.login__btn {
  transition: color 0.3s ease;
  user-select: none;
}
.login__btn:hover {
  color: var(--white-color);
}
.login__btn:hover::after {
  color: var(--graywhite-color);
  pointer-events: none;
}
.login__btn:hover ~ .login__icon {
  fill: var(--white-color);
}
.login__btn:first-child {
  margin-right: 0.625rem;
}

.os-scrollbar {
  --os-size: 5px;
  --os-padding-perpendicular: 0;
  --os-padding-axis: 0;
  --os-track-border-radius: 0;
  --os-track-bg: var(--gray300-color);
  --os-track-bg-hover: var(--gray300-color);
  --os-track-bg-active: var(--gray300-color);
  --os-track-border: none;
  --os-track-border-hover: none;
  --os-track-border-active: none;
  --os-handle-border-radius: 4px;
  --os-handle-bg: var(--gray700-color);
  --os-handle-bg-hover: var(--gray800-color);
  --os-handle-bg-active: var(--gray900-color);
  --os-handle-border: none;
  --os-handle-border-hover: none;
  --os-handle-border-active: none;
  --os-handle-min-size: 33px;
  --os-handle-max-size: none;
  --os-handle-perpendicular-size: 100%;
  --os-handle-perpendicular-size-hover: 100%;
  --os-handle-perpendicular-size-active: 100%;
  --os-handle-interactive-area-offset: 0;
}

.header-mid {
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  background-color: var(--white-color);
}
.header-mid__container {
  display: grid;
  column-gap: 1.25rem;
  align-items: center;
  grid-template-columns: 1.08fr 2.91fr minmax(auto, 23.75rem) 1fr;
}
.header-mid__logo {
  justify-self: self-start;
  white-space: nowrap;
}
.header-mid__nav {
  justify-self: self-start;
  white-space: nowrap;
}
.header-mid__choice {
  position: relative;
  display: flex;
  align-items: center;
  justify-self: self-end;
  white-space: nowrap;
}
.header-mid__choice::before {
  content: "";
  position: absolute;
  left: 3.625rem;
  top: 50%;
  width: 0.0625rem;
  height: 1.375rem;
  background-image: var(--divider-gradient);
  transform: translateY(-50%);
}

.logo {
  width: 8.125rem;
  height: 1.375rem;
}

.nav__list {
  display: flex;
  column-gap: 2.5rem;
  align-items: center;
  justify-content: space-between;
}
.nav__item {
  position: relative;
}
.nav__item:not(:last-child):hover::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 100%;
  width: calc(100% + 5rem);
  height: 5rem;
  transform: translate(-50%, -50%);
}
.nav__item:not(:last-child):hover .megamenu {
  display: block;
}
.nav__link {
  position: relative;
  display: inline-block;
  font: var(--base-bold);
  color: var(--gray800-color);
}
.nav__link:hover {
  color: var(--primary-color);
}
.nav__link--sale {
  color: var(--danger-color);
}
.nav__link--sale:hover {
  color: var(--danger-color);
}
.nav__megamenu {
  position: fixed;
  left: 0;
  top: 7.875rem;
  z-index: var(--z-fixed);
}

.search-form {
  position: relative;
  width: 100%;
}
.search-form__input {
  width: 100%;
  height: 2.75rem;
}
.search-form__btn {
  position: absolute;
  right: 1.0625rem;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
.search-form__icon {
  vertical-align: middle;
  width: 1rem;
  height: 1rem;
}

.favorite {
  display: flex;
  align-items: center;
  margin-right: 2.5625rem;
}
.favorite__btn {
  margin-right: 0.5625rem;
}
.favorite__icon {
  display: block;
  width: 1.1875rem;
  height: 1.03125rem;
}
.favorite__count {
  font: var(--base-regular);
  user-select: none;
}

.cart {
  display: flex;
  align-items: center;
}
.cart__btn {
  margin-right: 0.5rem;
}
.cart__icon {
  display: block;
  width: 1.3125rem;
  height: 1.375rem;
}
.cart__count {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.25rem;
  padding-inline: 0.1875rem;
  height: 1.25rem;
  min-width: 1.4375rem;
  font: var(--extrasmall-bold);
  color: var(--white-color);
  background-color: var(--success-color);
  user-select: none;
}

.megamenu {
  display: none;
  border-top: 1px solid var(--gray300-color);
  border-radius: 0 0 0.25rem 0.25rem;
  padding-top: 1.3125rem;
  padding-bottom: 2.5rem;
  width: 100%;
  box-shadow: var(--large-shadow);
  background-color: var(--white-color);
}
.megamenu::before {
  content: "";
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.megamenu__container {
  display: grid;
  grid-template-columns: 7.15fr 2.85fr;
}
.megamenu__categories {
  position: relative;
  display: grid;
  column-gap: 1.25rem;
  grid-template-columns: repeat(4, minmax(auto, 11.875rem));
  padding-right: 1.25rem;
}
.megamenu__categories::before {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  width: 0.0625rem;
  height: 26.6875rem;
  background-image: var(--divider-gradient);
  transform: translateY(-50%);
}
.megamenu__title {
  margin-bottom: 0.5rem;
  font: var(--small-bold);
  letter-spacing: 0.0625rem;
  text-transform: uppercase;
  color: var(--gray900-color);
}
.megamenu__title-link:hover {
  color: var(--primary-color);
}
.megamenu__item:not(:last-child) {
  margin-bottom: 0.5rem;
}
.megamenu__link {
  font: var(--small-regular);
  color: var(--gray800-color);
}
.megamenu__link:hover {
  color: var(--primary-color);
}
.megamenu__link--sale {
  color: var(--danger-color);
}
.megamenu__link--sale:hover {
  color: var(--danger-color);
}
.megamenu__offer {
  justify-self: self-end;
  padding-left: 1.5625rem;
}

.offer__image {
  margin-bottom: 1rem;
}
.offer__descr {
  margin-bottom: 1rem;
  font: var(--small-bold);
  color: var(--gray800-color);
}
.offer__btn svg {
  width: 0.75rem;
  height: 0.46875rem;
}

.header-bot {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  background-color: var(--primary-color);
}
.header-bot__header-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.marketing-slider {
  position: relative;
  color: var(--white-color);
}
.marketing-slider__list {
  overflow-y: hidden;
  height: 1.125rem;
}
.marketing-slider__list::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.marketing-slider__slide {
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
}
.marketing-slider__slide--visible {
  width: auto;
  height: 100%;
  opacity: 1;
  visibility: visible;
}
.marketing-slider__text {
  margin-right: 0.1875rem;
  font: var(--extrasmall-bold);
  user-select: none;
}
.marketing-slider__link {
  font: var(--extrasmall-regular);
  text-decoration: underline;
}
.marketing-slider__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.marketing-slider__btn svg {
  width: 0.65625rem;
  height: 0.375rem;
  fill: currentcolor;
  pointer-events: none;
}
.marketing-slider__btn--left {
  left: -1.5rem;
  transform: translateY(-50%) rotate(90deg);
}
.marketing-slider__btn--right {
  right: -1.5rem;
  transform: translateY(-50%) rotate(-90deg);
}

.hero {
  padding-top: 10.125rem;
  width: 100%;
  height: 50rem;
  background-image: url("../img/hero1.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}